if (document.querySelector('.about-info-image')) {
  console.log("yes");
  let about_imgs = document.querySelectorAll('.about-info-image');
  about_imgs.forEach(img => {
    //img.classList.remove('about-info-image--active');
    img.addEventListener('mouseover', function() {
      console.log("hover");
      let active_img =  document.querySelector('.about-info-image--active');
      if (active_img) {
        active_img.classList.toggle('about-info-image--active');
      }
      img.classList.toggle('about-info-image--active');
  })
})
}